'use client'
import { Link } from "@nextui-org/link";
import { Snippet } from "@nextui-org/snippet";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownSection,
  DropdownItem
} from "@nextui-org/dropdown";
import { Code } from "@nextui-org/code";
import { button as buttonStyles } from "@nextui-org/theme";
import { Card, CardFooter, Image, Button } from "@nextui-org/react";
import { Popover, PopoverTrigger, PopoverContent, Input } from "@nextui-org/react";
import { Progress, Slider } from "@nextui-org/react";
import { Tooltip } from "@nextui-org/react";
import { TimeInput } from "@nextui-org/react";
import { TrendingUpIcon } from "@/components/icons";
import { GithubIcon } from "@/components/icons";
import React, { useState, useRef, useEffect } from "react";
import { Chip } from "@nextui-org/react";

export default function Home() {
  const [selectedTimer, setSelectedTimer] = useState<string>("medium");
  const [timerValue, setTimerValue] = useState<number>(25);
  const [isPopoverOpen, setIsPopoverOpen] = useState(false);
  const [customTime, setCustomTime] = useState<string>("");
  const [currentState, setCurrentState] = useState<string>("neutral");
  const [remainingTime, setRemainingTime] = useState<number>(timerValue * 60);

  const inputRef = useRef<HTMLInputElement>(null);

  const handleSelectionChange = (keys: Set<string>) => {
    const selectedKey = Array.from(keys)[0];
    if (selectedKey !== "custom") {
      setSelectedTimer(selectedKey);
      let newTimerValue = 25;
      switch (selectedKey) {
        case "extra-short":
          newTimerValue = 5;
          break;
        case "short":
          newTimerValue = 15;
          break;
        case "medium":
          newTimerValue = 25;
          break;
        case "long":
          newTimerValue = 35;
          break;
        case "extra-long":
          newTimerValue = 45;
          break;
        default:
          newTimerValue = 25;
      }
      setTimerValue(newTimerValue);
      setRemainingTime(newTimerValue * 60);
      setCurrentState("neutral");
    }
  };

  const handleCustomSelect = (e: React.MouseEvent<HTMLLIElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsPopoverOpen(true);
  };

  const handleCustomTimeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setCustomTime(e.target.value);
  };

  const handleCustomTimeSubmit = () => {
    if (customTime) {
      setSelectedTimer(`custom-${customTime}`);
      const customValue = Number(customTime);
      setTimerValue(customValue);
      setRemainingTime(customValue * 60);
      setIsPopoverOpen(false);
      setCurrentState("neutral");
    }
  };

  const startTimer = () => {
    setCurrentState("timer_running");
    const interval = setInterval(() => {
      setRemainingTime((prevTime) => {
        if (prevTime === 0) {
          clearInterval(interval);
          setCurrentState("timer_finished");
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
  };

  const startBreak = () => {
    setCurrentState("break_running");
    const breakDuration = 5; // Break duration in minutes
    setRemainingTime(breakDuration * 60);
    const interval = setInterval(() => {
      setRemainingTime((prevTime) => {
        if (prevTime === 0) {
          clearInterval(interval);
          setCurrentState("neutral");
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
  };

  const resetTimer = () => {
    setCurrentState("neutral");
    setRemainingTime(timerValue * 60);
  };

  useEffect(() => {
    if (isPopoverOpen) {
      inputRef.current?.focus();
    }
  }, [isPopoverOpen]);

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  return (
    <section className="flex flex-col items-center justify-center gap-4 py-8 md:py-10">
      
      <div className="inline-block text-center justify-center" style={{ width: '30%' }}>
        <Card radius="lg" className="p-4">
          <div className="relative z-10">
          <div>
            <Tooltip content="This is a tooltip">
              <Chip>
                <TrendingUpIcon className="size-6" />
              </Chip>
            </Tooltip>
          </div>
            <br></br>
            <div>
              <span className={title({ size: "lg", fullWidth: true })}>
                {formatTime(remainingTime)}
              </span>
            </div>

            <Progress
              size="sm"
              value={timerValue * 60 - remainingTime}
              maxValue={timerValue * 60}
              color="warning"
              className="max-w-md"
              style={{ width: '85%', margin: 'auto', marginTop: '15px', visibility: currentState === "neutral" ? 'hidden' : 'visible'}}
            />

            <div className="mt-4 flex gap-2 justify-center">
              {currentState === "neutral" && (
                <>
                  <Button onPress={startTimer} color="primary" variant="flat">Start Session</Button>
                  <Dropdown>
                    <DropdownTrigger>
                      <Button variant="flat">Adjust Timer Length</Button>
                    </DropdownTrigger>
                    <DropdownMenu
                      variant="flat"
                      disallowEmptySelection
                      selectionMode="single"
                      selectedKeys={new Set([
                        selectedTimer.startsWith("custom-") ? "custom" : selectedTimer,
                      ])}
                      onSelectionChange={(keys) =>
                        handleSelectionChange(keys as Set<string>)
                      }
                    >
                      <DropdownItem key="extra-short">
                        Extra Short Session
                        <span style={{ color: "gray", float: "right" }}>5m</span>
                      </DropdownItem>
                      <DropdownItem key="short">
                        Short Session
                        <span style={{ color: "gray", float: "right" }}>15m</span>
                      </DropdownItem>
                      <DropdownItem key="medium">
                        Medium Session
                        <span style={{ color: "gray", float: "right" }}>25m</span>
                      </DropdownItem>
                      <DropdownItem key="long">
                        Long Session
                        <span style={{ color: "gray", float: "right" }}>35m</span>
                      </DropdownItem>
                      <DropdownItem key="extra-long">
                        Extra Long Session&nbsp;&nbsp;&nbsp;
                        <span style={{ color: "gray", float: "right" }}>45m</span>
                      </DropdownItem>
                      <DropdownItem key="custom" onClick={handleCustomSelect}>
                        Custom Session
                        <span style={{ color: "gray", float: "right" }}>
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 30 30"
                            strokeWidth={1.5}
                            stroke="currentColor"
                            className="size-6"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              d="M12 6v6h4.5m4.5 0a9 9 0 1 1-18 0 9 9 0 0 1 18 0Z"
                            />
                          </svg>
                        </span>
                      </DropdownItem>
                    </DropdownMenu>
                  </Dropdown>
                </>
              )}

              {currentState === "timer_running" && (
                <>
                  <Button onPress={resetTimer} color="danger" variant="light">Reset</Button>
                </>
              )}

              {currentState === "timer_finished" && (
                <>
                  <Button onPress={startBreak} color="success" variant="shadow">Claim Break</Button>
                  <Button onPress={startBreak} color="default" variant="flat">Forfeit Break</Button>
                </>
              )}

              {currentState === "break_running" && (
                <>
                  <Button onPress={resetTimer} color="danger" variant="light">End Break</Button>
                </>
              )}
            </div>

            {isPopoverOpen && (
              <Popover
                isOpen={isPopoverOpen}
                onOpenChange={setIsPopoverOpen}
                placement="bottom"
              >
                <PopoverTrigger>
                  <div></div>
                </PopoverTrigger>
                <PopoverContent className="p-2 flex items-center">
                  <div className="flex w-full justify-between">
                    <Input
                      type="number"
                      value={customTime}
                      onChange={handleCustomTimeChange}
                      placeholder="Enter minutes"
                      min={1}
                      onKeyPress={(e) => {
                        if (e.key === 'Enter') {
                          handleCustomTimeSubmit();
                        }
                      }}
                      ref={inputRef}
                      className="w-full"
                    />
                    <Button onClick={handleCustomTimeSubmit} className="ml-2">Save</Button>
                  </div>
                </PopoverContent>
              </Popover>
            )}
          </div>
        </Card>
      </div> 

    </section>
  );
}
